import React from 'react';
import {NavLink} from "react-router-dom";

function JoinList(props) {
    return (
        <div>
            <div>
                <h1></h1>
            </div>
            <div>
                <ul>
                    <li>&nbsp;&nbsp;<NavLink to={'/join/form'}><button type='button' className='btn btn-sm btn-success'></button></NavLink></li>
                    
                </ul>
            </div>
        </div>
    );
}

export default JoinList;